export default {
	data() {
		return {
			phone: '',
			pwd: '',
		}
	},
	onBackPress(event){
		// if(event.from == "backbutton"){
		// 	uni.showModal({  
		// 	    title: '提示',  
		// 	    content: '是否退出uni-app？',  
		// 	    success: function(res) {  
		// 	        if (res.confirm) {  
		// 	            // 退出当前应用，改方法只在App中生效  
		// 	            plus.runtime.quit();  
		// 	        } else if (res.cancel) {  
		// 	            console.log('用户点击取消');  
		// 	        }  
		// 	    }  
		// 	});
		// 	return true  
		// }
		if(event.from == "backbutton"){
			uni.switchTab({
				url:'../index/index'
			})
		}
		console.log(event)
	},
	methods: {
		logIn(){
			uni.request({
				url: this.url + '/mobile/login',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				},
				data:{
					type:'userCodeLogin',
					userCode:this.phone,
					password:this.pwd
				},
				success: (res) => {
					console.log(res)
					if(res.data.result){
						uni.showToast({
							title: '登录成功',
							duration: 2000,
						});
						uni.setStorageSync('token',res.data.data)
						uni.switchTab({
							url:'../index/index'
						})
					}else{
						uni.showToast({
							title: res.data.msg,
							icon:'none',
							duration: 2000,
						});
					}
				},
				error:function(err){
				    uni.showToast({
				    	title: '登录失败，请稍后再试',
						icon:'none',
				    	duration: 2000,
				    });
				},
			})
		}
	}
}
